*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Inter', sans-serif;
  position: relative;
}

.header{
  background-color: whitesmoke;
  height: 100px;
  transition: 1s ease-in-out;
}

.header-container{
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 100%;
  width: 100%;
}

.logo{
  font-size: 2.5rem;
  transition: 1s ease-in-out;
  padding-left: 1rem;
}

.nav-container{
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: 0.7s ease-in-out;
}
.menu{
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: 0.7s ease-in-out;
}

.nav-items{
  margin-right: 20px;
}
.nav-items2{
  margin-right: 20px;
}

.nav-items ul{
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  width: 600px;
}
.nav-items2 ul{
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  width: 600px;
}

.nav-items ul li{
  text-decoration: none;
}
.nav-items ul li a{
  text-decoration: none;
}

.nav-items2 ul li{
  text-decoration: none;
}

.nav-items2 ul li a{
  text-decoration: none;
}


.header-form form{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 40px;
}
.search-form{
  height: 100%;
  display: flex;
  justify-content:flex-start ;
  position: relative;
  border-radius: 5px;
  width: 250px;
}

.input-text{
  display: inline-block;
  padding: 5px 10px;
  width: 40px;
  border: none;
  border-radius: 10px;
  height: 100%;
  visibility: hidden;
}

.activated{
  width: 200px;
  transition: 1s ease-in-out;
  visibility: visible;
}

.search-button{
  display: flex;
  position: absolute;
  height: 100%;
  width: 45px;
  top: 0;
  right: 41%;
  border-radius: 5px;
  align-items: center;
  justify-content: center;
  border: none;
  transition: 1s ease-in-out;
  color: white;
  background-color: rgb(146, 146, 146);
}

.searchHover{
  right: 0;
}
.search-button:focus{
  border: none;
}


.search-section-container .header-form .search-form{
  display: flex;
  justify-content:flex-start;
  width: 360px;
}

.search-section-container .header-form .search-form .search-button{
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}
.search-section-container .header-form .activated{
  width: 300px;
  border: none;
}

.search-section-container .icon{
  height: 100%;
  width: 45px;
    display: flex;
    position: absolute;
    padding: 10px;
}


@media screen and(max-width:450px) {
.search-section-container .header-form .activated {
  width: 200px;
  border: none;
}
.search-section-container .header-form .search-form {
  display: flex;
  justify-content: flex-start;
  width: 300px;
}
}

.hamburger{
  position: absolute;
  width: 0%;
  visibility: hidden;
}

.link{
  cursor: pointer;
  width: 100px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
}

.link:hover{
  background-color: rgb(185, 255, 255);
}

.active{
  text-decoration: none;
}

a{
  text-decoration: none;
}

.active .link{
  background-color: aqua;
  width: 100px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
}

