/* ! Search Section */
.search-section{
  width: 100%;
  height: 1000px;
  background-color: aqua;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-image:linear-gradient(to bottom, rgba(29, 29, 29, 0.15) 10%,
    rgb(8, 1, 1,0.9)),  url("./images/search-section.jpeg");
}
.search-section-container{
  width: 80%;
  height: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.search-section-container h2{
  font-size: 4rem;
  margin-bottom: 2rem;
  color: rgb(241, 241, 241);
  text-shadow: 2px 2px  black;
  text-align: center;
}

.search-section-container h3{
  font-size: 3rem;
  margin-bottom: 2rem;
  color: rgb(241, 241, 241);
  text-shadow: 2px 2px  black;
  text-align: center;
}

.search-section-container form input{
  display: inline-block;
  padding: 15px 25px;
  border-radius: 10px;
  border: none;
}
.search-section-container form button{
  display: inline-block;
  padding: 15px 25px;
}



/* ?Cuisine Section */
.cuisine-section{
  width: 100%;
  height: 1000px;
  margin-top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: whitesmoke;
  
}

.cuisine-background{
  background-attachment: fixed;
    background-repeat: repeat;
    background-position: center;
    background-image:url("./images/cuisine-section.jpeg");
    width: 50%;
    height: 100%;
}


.cuisine-section-container {
  width: 50%;
  height: 60%;
  padding: 0 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}



.animated-text{
  font-size: 3rem;
  margin-bottom: 2rem;
  text-align: center;
  background: linear-gradient(-45deg, blue, red);
  background-size: 300%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  
  animation: move 5s ease-in-out infinite;
}

.cuisine-link{
  width: 120px;
  height: 45px;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 5px solid aqua;
  font-weight: bold;
}




@keyframes move {
  0% {background-position: 0 50%;}
  50% {background-position: 100% 50%;}
  100% {background-position: 0 50%;}
}


/* !CUISINE PAGE RESULTS */

.cuisine-page{
  display: flex;
}

.cuisine-page ul{
  width: 15%;
}
.cuisine-page ul li {
  width: 100%;
}
.cuisine-page ul li button{
  width: 100%;
}

.cuisine-right{
  width: 85%;
}

.cuisine-page-results{
  width: 100%;
  display: grid;
  grid-template-columns: auto auto auto;
  text-align: center;
}

.cuisine-page-results div{
  width: 350px;
  margin: 0 auto;
}

.chosen-cuisine{
  background-color: red;
}


/* !MEAL PLANNER */
.meal-container{
  height: 1000px;
}

